import react, {Component} from 'react';

import Header from './header';
import {Image} from 'bloomer'




export default props=>{
  return( <div style={{backgroundImage: "url('/static/images/background.png')", backgroundSize: 'cover', width: '100%', height:'100%'}}>
  
  <Header>
    {props.children}
    </Header>
    </div>)
}


